.row {
  padding: 3em 0px 5.8em 0px;
}
.pagination {
  display: flex;
  margin: auto;
  width: 17%;
}

.pagination a {
  color: rgb(42 43 141);
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  border: 1px solid white;
  border-radius: 5px;
}

li {
  margin-right: 0.5rem;
}
.pagination li.active {
  background-color: rgb(42 43 141);
  color: white;
  border-radius: 5px;
}

.pagination a:hover:not(.active) {
  background-color: #ddd;
  border-radius: 5px;
  color: #fff;
}
.pagination li.active a {
  color: #fff;
}
.row {
  margin: auto;
  width: 50%;
}
.invisible {
  display: none;
}

body {
  background-color: rgb(5 4 23);
}
/********************IMAGES DIV*********************/

.imagesDiv {
  /* Prevent vertical gaps */
  line-height: 0;

  -webkit-column-count: 3;
  -webkit-column-gap: 0px;
  -moz-column-count: 5;
  -moz-column-gap: 0px;
  column-count: 3;
  grid-column-gap: 0px;
  column-gap: 0px;
  width: -webkit-min-content;
  width: min-content;
}
.imagesDiv-wrapper {
  background-color: #ffffff;
}
.imagesDiv {
  margin: auto;
  padding-top: 1.7%;
}

/********************MODAL*********************/

.photo {
  /* Just in case there are inline attributes */
  /* width: 100% !important;*/
  height: auto !important;
  margin: 4px;
}
.modal-photo {
  width: 65vh;
  height: 80vh;
}
.user-photo {
  border-radius: 40px;
}

.user-names {
  margin-left: 0.7rem;
}
.userinfo-row {
  display: flex;
  /* justify-content: flex-start; */
  align-items: center;
  margin-top: 3%;
}
.MuiButton-label {
  color: #2a2b8d;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
}
.download-btn {
  margin-left: auto;
}

.firstName {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-size: 1.3rem;
  line-height: 40, 85px;
}
.userName {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-size: 1rem;
  line-height: 27, 24px;
  font-weight: bold;
  color: #2a2b8d;
}

.loc-wrapper {
  font-family: Open Sans;
  font-size: 0.9em;
  line-height: 19px;

  color: #050417;
  padding: 4% 0;
  display: flex;
}
.loc-wrapper img {
  margin-right: 1%;
}
.modal-error {
  width: 95%;
  height: 6vh;
  background-color: #ffeded;
  border-radius: 4px;
  margin: auto;
  font-family: "Open Sans";
  font-style: "normal";
  font-weight: 600;
  font-size: 14px;
}
.modal-error p {
  color: #ff4a4a;
  text-align: left;
  margin: 10px 20px;
  display: block;
  padding: 3% 2%;
}
a {
  text-decoration: none;
}

